import React, { Fragment } from 'react';
import Socials from '../Socials/Socials';
import { TiLocation} from "react-icons/ti";
import { Link } from 'react-router-dom';
import Logo from '../SideNav/isan-logo.png';
import './Topbar.css';

const Topbar = (props) => {
    return (
        <Fragment>
            <div className='logo2'>
                <Link to="/">
                    <img src={Logo} 
                    alt="Logo2" onClick={props.drawerToggle} />
                </Link>
            </div>
            <div className='topbar'>                
                <div className='twins'>
                    <div className='tb-phone'>
                        <a href='tel:+233244444444'>CUSTOMER SERVICE</a>
                    </div>
                    <div className='tb-location'>
                        <a href='https://maps.google.com'>LOCATION <TiLocation size={15} /></a>
                    </div>
                </div>
                <div className='spacer' />
                <div className='tb-socials'>
                    <Socials />
                </div>
            </div>
        </Fragment>
    )
}

export default Topbar;