import React, { Component } from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';
import Topbar from './components/navigation/Topbar/Topbar';
import Backdrop from './components/navigation/Backdrop/Backdrop';
import SideNav from './components/navigation/SideNav/SideNav';
import { FiMenu } from "react-icons/fi";
import Logo from './components/navigation/SideNav/isan-logo.png';
import Home from './components/pages/Home/Home';
import AfricanJackets from './components/pages/AfricanJackets/AfricanJackets';
import AfricanHoodie from './components/pages/AfricanHoodie/AfricanHoodie';
import BomberJackets from './components/pages/BomberJackets/BomberJackets';
import MilitaryJackets from './components/pages/MilitaryJackets/MilitaryJackets';
import ShopWholesale from './components/pages/ShopWholesale/ShopWholesale';
import BikerJackets from './components/pages/BikerJackets/BikerJackets';
import Sweatshirts from './components/pages/Sweatshirts/Sweatshirts';
import AfricanSneakers from './components/pages/AfricanSneakers/AfricanSneakers';
import LeatherSneakers from './components/pages/LeatherSneakers/LeatherSneakers';
import './App.css';

class App extends Component {
  constructor () {
    super ()
    this.state = {
      sideDrawerOpen: false,
    }
  }

  drawerToggleClicked = () => {
    this.setState((prevState) => {
      return {sideDrawerOpen: !prevState.sideDrawerOpen};
    });
  }

  backdropClicked = () => {
    this.setState({sideDrawerOpen: false})
  }

  render () {
    let backdrop;

    if (this.state.sideDrawerOpen) {
      backdrop = <Backdrop backdropClick={this.backdropClicked} />
    }

    return (
      <BrowserRouter>
        <div className='app-container'>
          {backdrop}
          <header>
            <div className="app-topbar">
              <div className='menu-button'>
                <FiMenu
                  size={30}
                  onClick={this.drawerToggleClicked} />
              </div>
              <Topbar drawerToggle={this.backdropClicked} />
            </div>
            <nav className='app-navbar'>
              <div className='emptySpace'></div>
              <div className='logo1'>
                <Link to="/">
                  <img src={Logo} 
                  alt="Logo" />
                </Link>
              </div>
              <SideNav
                show={this.state.sideDrawerOpen}
                drawerToggle={this.backdropClicked} />
            </nav>
          </header>
  
          <main className='page-content'>
            <Route exact path='/' render={props => (
              <React.Fragment> <Home /> </React.Fragment>
            )} />
            <Route path='/africanJackets' component={AfricanJackets} />
            <Route path='/africanHoodie' component={AfricanHoodie} />
            <Route path='/bomberJackets' component={BomberJackets} />
            <Route path='/militaryJackets' component={MilitaryJackets} />
            {/* The following pages would be hidden for the mean time */}
            <Route path='/bikerJackets' component={BikerJackets} />
            <Route path='/sweatshirts' component={Sweatshirts} />
            <Route path='/africanSneakers' component={AfricanSneakers} />
            <Route path='/leatherSneakers' component={LeatherSneakers} />
            {/* Excluding this last page */}
            <Route path='/shopWholesale' component={ShopWholesale} />
          </main>
        </div> {/* End of Container */}
      </BrowserRouter>
    )
  }
}

export default App;