import React from 'react';
import { Link } from 'react-router-dom';
import './SideNav.css';

const SideNav = (props) => {
	let drawerClasses = 'side-drawer';
	if (props.show) {
		drawerClasses = 'side-drawer open';
	}

	return (
		<div className= {`side-nav ${drawerClasses}`} >
			<ul>
				<li onClick={props.drawerToggle}><Link to="/africanJackets" className='link'>African Jackets</Link></li>
				<li onClick={props.drawerToggle}><Link to="/africanHoodie" className='link'>African Hoodie</Link></li>
				<li onClick={props.drawerToggle}><Link to="/bomberJackets" className='link'>Bomber Jackets</Link></li>
				<li onClick={props.drawerToggle}><Link to="/militaryJackets" className='link'>Military Jackets</Link></li>
				{/*The following nav items are for future use*/}
				{/* <li onClick={props.drawerToggle}><Link to="/bikerJackets" className='link'>Biker Jackets</Link></li>
				<li onClick={props.drawerToggle}><Link to="/sweatshirts" className='link'>Sweat shirts</Link></li>
				<li onClick={props.drawerToggle}><Link to="/africanSneakers" className='link'>African Sneakers</Link></li>
				<li onClick={props.drawerToggle}><Link to="/leatherSneakers" className='link'>Leather Sneakers</Link></li> */}
				<li onClick={props.drawerToggle}><Link to="/shopWholesale" className='link'>Shop Wholesale</Link></li>
			</ul>
		</div>
	)
}

export default SideNav;